

























import Vue from "vue";

import Filter from "../../interfaces/Filter";

import TQGlobals from "../../classes/TQGlobals";

import ItemLink from "../ItemLink.vue";

export default Vue.extend({

    props: {
        filter: {
            type: Object as () => Filter,
            required: true
        },
        itemName: {
            type: String,
            required: true
        },
    },

    data: function () {
        return {
            isKnown: true
        }
    },

    mounted: function () {
        this.isKnown = this.filter.metaData.itemIsKnown[this.itemName] as boolean;
    },

    methods: {

        onRemoveClicked: function () {
            let index = this.filter.settings.items.names.indexOf(this.itemName);
            if (index >= 0) {
                this.filter.settings.items.names.splice(index, 1);
                TQGlobals.filterManager.saveUserFilters();
            }
        },

    },

    components: {
        ItemLink
    }
});
